* {
  padding: 0;
  margin: 0;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*::before, *::after {
  box-sizing: initial;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

h1, h2 {
  font-family: "Secular One", sans-serif;
  font-weight: 400;
}

p {
  font-family: "Figtree", sans-serif;
  font-weight: 400;
  line-height: 2;
  font-size: 1.5rem;
  color: #424488;
}

a {
  font-family: "Figtree", sans-serif;
  font-weight: 600;
  text-decoration: none;
}

img {
  display: block;
  width: 100%;
}

.separacion {
  margin-top: 10rem;
}

.center {
  text-align: center;
  font-size: 3rem;
  color: #011165;
}
@media (min-width: 768px) {
  .center {
    width: 70%;
    margin: 0 auto;
  }
}

.boton {
  display: block;
  width: 100%;
  background-color: #f4694b;
  color: #FFFFFF;
  border-radius: 0.5rem;
  padding: 1rem 2rem;
  text-align: center;
  font-size: 1.5rem;
  transition-property: background-color, color;
  transition-duration: 0.7s;
  border: none;
}
.boton:hover {
  background-color: #FFFFFF;
  color: #000000;
  cursor: pointer;
}
@media (min-width: 768px) {
  .boton {
    display: inline;
  }
}

.abajo {
  background-color: #011165;
  position: fixed;
  width: 100%;
  top: 0;
  padding: 2rem 0;
  left: 0;
  right: 0;
  z-index: 999999;
  box-shadow: 10px 10px 10px 10px rgba(0, 0, 0, 0.2);
}

.header {
  padding: 2rem 0;
  background-color: #011165;
}
@media (min-width: 1280px) {
  .header__contenedor {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.header__logos {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 1280px) {
  .header__logos {
    display: unset;
  }
}
.header__mobile {
  display: flex;
  gap: 5rem;
}
@media (min-width: 1280px) {
  .header__mobile {
    display: none;
  }
}
.header__botones {
  display: none;
}
@media (min-width: 1280px) {
  .header__botones {
    display: flex;
    align-items: center;
    gap: 2rem;
  }
}
.header__menu {
  cursor: pointer;
}
.header__buscador {
  width: 2rem;
  height: 2em;
}

.navegacion {
  margin: 3rem 0;
  display: none;
}
@media (min-width: 1280px) {
  .navegacion {
    display: unset;
    margin: 0;
  }
}
.navegacion__link {
  color: #FFFFFF;
  display: block;
  margin-bottom: 2rem;
  font-size: 1.6rem;
  text-align: center;
}
@media (min-width: 1280px) {
  .navegacion__link {
    display: inline;
    margin-right: 2rem;
    margin-bottom: 0;
  }
}

.informacion {
  margin: 5rem auto;
}
@media (min-width: 768px) {
  .informacion {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 3rem;
    align-items: center;
  }
}
.informacion__heading {
  font-size: 2.5rem;
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .informacion__heading {
    font-size: 4rem;
  }
}
@media (min-width: 1280px) {
  .informacion__heading {
    font-size: 6rem;
  }
}
.informacion__txt {
  color: #FFFFFF;
}
.informacion__imagenes {
  display: flex;
  margin: 3rem 0;
}
.informacion__img {
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  border: 0.3rem solid #FFFFFF;
  margin-left: -1rem;
}
.informacion__img:nth-child(1) {
  margin-left: 0;
}
.informacion__footer {
  color: #FFFFFF;
}
.informacion__formulario {
  background-color: #FFFFFF;
  padding: 5rem 2rem;
  border-radius: 1rem;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .informacion__formulario {
    margin-top: 0;
    width: 70%;
    margin: 0 auto;
  }
}
.informacion__title {
  font-size: 3rem;
  text-align: center;
  margin-bottom: 3rem;
}
.informacion__input {
  display: block;
  width: 90%;
  border-radius: 0.5rem;
  margin-bottom: 2rem;
  border: none;
  outline: none;
  padding: 2rem 1rem;
  font-size: 1.5rem;
  background-color: #F2F8FF;
}
.informacion__boton {
  transition-property: background-color, color;
  transition-duration: 0.8s;
}
.informacion__boton:hover {
  background-color: #011165;
  color: #FFFFFF;
}
.informacion__footer--negro {
  margin: 2rem 0;
  text-align: center;
}

@media (min-width: 768px) {
  .marketing__heading {
    font-size: 5rem;
  }
}
.marketing__grid {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .marketing__grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 3rem;
    align-items: center;
  }
}
.marketing__card {
  padding: 5rem 2rem;
  background-color: #FFFFFF;
  box-shadow: 10px 10px 50px 10px rgba(0, 0, 0, 0.1);
  margin-bottom: 3rem;
}
@media (min-width: 768px) {
  .marketing__card {
    margin-bottom: 0;
  }
}
.marketing__img {
  width: 1rem;
  height: 1rem;
  background-color: rgb(255, 221, 251);
  padding: 1rem;
  margin-bottom: 2rem;
}
.marketing__title {
  font-size: 2rem;
  color: #011165;
  margin: 2rem 0;
}

.principal__grid {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .principal__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
    gap: 3rem;
  }
}
.principal__imagen {
  background-color: rgb(255, 221, 251);
  border-radius: 100%;
  width: 90%;
  margin: 0 auto;
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .principal__imagen {
    margin-bottom: 0;
  }
}
.principal__img {
  width: 70%;
  margin: 0 auto;
  animation: imagen 2s ease-in-out infinite;
}
.principal__img--uno {
  position: relative;
}
.principal__img--dos {
  width: 100%;
  margin-top: -10rem;
}
@media (min-width: 1280px) {
  .principal__img--dos {
    margin-top: -20rem;
  }
}
.principal__informacion {
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .principal__informacion {
    margin-bottom: 0;
  }
}
.principal__heading {
  font-size: 3rem;
  color: #011165;
  text-align: center;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .principal__heading {
    text-align: left;
    font-size: 5rem;
  }
}
.principal__footer {
  margin-top: 3rem;
  display: flex;
  justify-content: space-between;
}
.principal__numero {
  color: #f4694b;
  font-size: 3rem;
  margin: 0;
  padding: 0;
}
.principal__descripcion {
  margin: 0;
  padding: 0;
  width: 70%;
  line-height: 1.4;
  color: #011165;
}
.principal__span {
  color: #424488;
}

@keyframes imagen {
  0% {
    top: -1%;
  }
  50% {
    top: 1%;
  }
  100% {
    top: -1%;
  }
}
.swiper {
  margin-top: 5rem;
  width: 100%;
  height: 100%;
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
}

.swiper-slide img {
  display: block;
  object-fit: cover;
}

.slider__heading {
  text-align: center;
  font-size: 3rem;
  color: #011165;
}
@media (min-width: 768px) {
  .slider__heading {
    font-size: 5rem;
    width: 60%;
    margin: 0 auto;
  }
}
.slider__card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: left;
  background-color: #FFFFFF;
  box-shadow: 50px 50px 60px 50px rgba(0, 0, 0, 0.1);
  padding: 4rem 2rem;
}
.slider__cliente {
  width: 10rem;
  height: 10rem;
  margin: 2rem 0;
}
.slider__txt {
  width: 80%;
  margin: 2rem 0;
  text-align: center;
}
.slider__autor {
  font-size: 1.6rem;
}

.final {
  background-color: #011165;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 60rem;
  border-radius: 1rem;
}
.final__heading {
  color: #FFFFFF;
  font-size: 3rem;
}
@media (min-width: 768px) {
  .final__heading {
    font-size: 5rem;
  }
}
.final__txt {
  text-align: center;
  font-size: 1.7rem;
  color: #FFFFFF;
  width: 90%;
}
@media (min-width: 768px) {
  .final__txt {
    width: 60%;
    margin: 0 auto;
  }
}
.final__formulario {
  margin-top: 3rem;
  display: flex;
  position: relative;
}
.final__input {
  padding: 2rem 1rem;
  outline: none;
  border-radius: 0.5rem;
  width: 30rem;
}
@media (min-width: 768px) {
  .final__input {
    width: 42rem;
  }
}
.final__boton {
  padding: 1.5rem 1rem;
  position: absolute;
  width: auto;
  left: 20rem;
  top: 0.5rem;
}
@media (min-width: 768px) {
  .final__boton {
    left: 32rem;
  }
}
.final__animacion {
  display: flex;
}
.final__azul {
  border-radius: 50%;
  width: 10rem;
  height: 10rem;
  background-color: #FFFFFF;
  animation: pelotas 0.8s linear infinite;
}
@media (min-width: 768px) {
  .final__azul {
    margin-left: 10rem;
  }
}
@media (min-width: 1280px) {
  .final__azul {
    margin-left: 15rem;
  }
}
.final__rosa {
  border-radius: 50%;
  width: 10rem;
  height: 10rem;
  background-color: rgb(255, 221, 251);
  animation: pelotas 0.8s linear infinite;
  margin-left: 10rem;
}
@media (min-width: 768px) {
  .final__rosa {
    margin-left: 30rem;
  }
}
@media (min-width: 1280px) {
  .final__rosa {
    margin-left: 70rem;
  }
}

@keyframes pelotas {
  0% {
    transform: scale(1);
  }
  25% {
    transform: scale(1.1);
  }
  50% {
    transform: scale(1.2);
  }
  75% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}
.footer {
  background-color: #F2F8FF;
  padding: 10rem 0;
}
.footer__grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 3rem;
}
@media (min-width: 768px) {
  .footer__grid {
    grid-template-columns: repeat(4, 1fr);
  }
}
.footer__heading {
  font-size: 3rem;
  margin: 1rem 0;
}
.footer__link {
  list-style: none;
  margin-bottom: 2rem;
  font-size: 1.8rem;
  color: #424488;
  cursor: pointer;
}
.footer__iconos {
  display: flex;
  gap: 2rem;
}
.footer__icono {
  background-color: #FFFFFF;
  width: 2rem;
  height: 2rem;
  padding: 0.5rem;
  border-radius: 50%;
}